import { useRef, useCallback } from 'react';
import { Logger } from '../../shared/logger';

interface UseLongPressOptions {
    onLongPress: () => void;
    onShortPress?: () => void;
    delay?: number;
}

interface UseLongPressResult {
    onMouseDown: (event: React.MouseEvent) => void;
    onMouseUp: (event: React.MouseEvent) => void;
    onMouseLeave: (event: React.MouseEvent) => void;
    onTouchStart: (event: React.TouchEvent) => void;
    onTouchEnd: (event: React.TouchEvent) => void;
    onTouchCancel: (event: React.TouchEvent) => void;
}

export function useLongPress({
    onLongPress,
    onShortPress,
    delay = 600
}: UseLongPressOptions): UseLongPressResult {
    const timeoutRef = useRef<NodeJS.Timeout | null>(null);
    const isLongPressRef = useRef(false);
    const isPressingRef = useRef(false);

    const startPress = useCallback(() => {
        if (isPressingRef.current) return;

        isPressingRef.current = true;
        isLongPressRef.current = false;

        // Î°±ÌîÑÎ†àÏä§ ÌÉÄÏù¥Î®∏ ÏãúÏûë
        timeoutRef.current = setTimeout(() => {
            if (isPressingRef.current) {
                isLongPressRef.current = true;
                onLongPress();
                Logger.debug('LONGPRESS', 'Î°±ÌîÑÎ†àÏä§ Ïã§ÌñâÎê®!');
            }
        }, delay);

        Logger.debug('LONGPRESS', 'Î°±ÌîÑÎ†àÏä§ ÏãúÏûë', { delay });
    }, [onLongPress, delay]);

    const endPress = useCallback(() => {
        if (!isPressingRef.current) return;

        isPressingRef.current = false;

        // ÌÉÄÏù¥Î®∏ Ï†ïÎ¶¨
        if (timeoutRef.current) {
            clearTimeout(timeoutRef.current);
            timeoutRef.current = null;
        }

        // Î°±ÌîÑÎ†àÏä§Í∞Ä Ïã§ÌñâÎêòÏßÄ ÏïäÏïòÎã§Î©¥ ÏàèÌîÑÎ†àÏä§ Ïã§Ìñâ
        if (!isLongPressRef.current && onShortPress) {
            onShortPress();
            Logger.debug('LONGPRESS', 'ÏàèÌîÑÎ†àÏä§ Ïã§ÌñâÎê®!');
        }

        isLongPressRef.current = false;
    }, [onShortPress]);

    const cancelPress = useCallback(() => {
        isPressingRef.current = false;

        if (timeoutRef.current) {
            clearTimeout(timeoutRef.current);
            timeoutRef.current = null;
        }

        isLongPressRef.current = false;
        Logger.debug('LONGPRESS', 'Î°±ÌîÑÎ†àÏä§ Ï∑®ÏÜåÎê®');
    }, []);

    // ÎßàÏö∞Ïä§ Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨
    const onMouseDown = useCallback((event: React.MouseEvent) => {
        if (event.button !== 0) return; // ÏôºÏ™Ω ÌÅ¥Î¶≠Îßå
        event.preventDefault();
        startPress();
    }, [startPress]);

    const onMouseUp = useCallback((event: React.MouseEvent) => {
        event.preventDefault();
        endPress();
    }, [endPress]);

    const onMouseLeave = useCallback((event: React.MouseEvent) => {
        cancelPress();
    }, [cancelPress]);

    // ÌÑ∞Ïπò Ïù¥Î≤§Ìä∏ Ìï∏Îì§Îü¨
    const onTouchStart = useCallback((event: React.TouchEvent) => {
        if (event.touches.length > 1) return; // Îã®Ïùº ÌÑ∞ÏπòÎßå
        event.preventDefault();
        startPress();
    }, [startPress]);

    const onTouchEnd = useCallback((event: React.TouchEvent) => {
        event.preventDefault();
        endPress();
    }, [endPress]);

    const onTouchCancel = useCallback((event: React.TouchEvent) => {
        cancelPress();
    }, [cancelPress]);

    return {
        onMouseDown,
        onMouseUp,
        onMouseLeave,
        onTouchStart,
        onTouchEnd,
        onTouchCancel
    };
}

export default useLongPress;