'use client';

import React, { useEffect, useState } from 'react';
import { useEditor, EditorContent, BubbleMenu } from '@tiptap/react';
import StarterKit from '@tiptap/starter-kit';
import Placeholder from '@tiptap/extension-placeholder';
import Focus from '@tiptap/extension-focus';
import Typography from '@tiptap/extension-typography';
import CharacterCount from '@tiptap/extension-character-count';
import Underline from '@tiptap/extension-underline';
import Image from '@tiptap/extension-image';
import { SlashCommand, slashSuggestion } from './SlashCommands';
import { Bold, Italic, Underline as UnderlineIcon, Strikethrough, Code, Link, Quote, Palette, MoreHorizontal, ImageIcon, Copy, Clipboard } from 'lucide-react';
import { Logger } from '../../../../shared/logger';
import { handleEditorKeyDown, bindShortcutsToEditor, ALL_SHORTCUTS } from './EditorShortcuts';
import { TaskList, TaskItem, Callout, Toggle, Highlight } from './AdvancedNotionFeatures';
import './MarkdownEditor.css';

// üî• ÏûëÍ∞Ä ÏπúÌôîÏ†Å TipTap ÏóêÎîîÌÑ∞ Ïä§ÌÉÄÏùº
const EDITOR_STYLES = {
  container: 'w-full h-full flex flex-col',
  editor: 'flex-1 p-6 prose max-w-none focus:outline-none text-gray-900 dark:text-gray-100',
  focused: 'prose-lg', // Ìè¨Ïª§Ïä§ Î™®ÎìúÏóêÏÑú Îçî ÌÅ∞ Í∏ÄÏûê
  placeholder: 'text-slate-400 pointer-events-none',
  bubble: 'flex flex-nowrap gap-1 p-2 bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-lg shadow-lg z-50 overflow-visible whitespace-nowrap',
  bubbleButton: 'px-2 py-1 text-sm bg-slate-100 hover:bg-slate-200 dark:bg-slate-700 dark:hover:bg-slate-600 rounded transition-colors flex items-center justify-center min-w-[30px]'
} as const;

interface MarkdownEditorProps {
  content: string;
  onChange: (content: string) => void;
  isFocusMode: boolean;
}

export function MarkdownEditor({ content, onChange, isFocusMode }: MarkdownEditorProps): React.ReactElement {
  const [isReady, setIsReady] = useState(false);
  const [isDragOver, setIsDragOver] = useState(false); // üî• ÎìúÎûòÍ∑∏ Ïò§Î≤Ñ ÏÉÅÌÉú Ï∂îÍ∞Ä

  // üî• ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ ÌîºÎìúÎ∞±ÏùÑ ÏúÑÌïú Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà
  useEffect(() => {
    const handleDragOver = (e: DragEvent) => {
      e.preventDefault();
      setIsDragOver(true);
    };

    const handleDragLeave = (e: DragEvent) => {
      e.preventDefault();
      // ÏóêÎîîÌÑ∞ ÏòÅÏó≠ÏùÑ ÏôÑÏ†ÑÌûà Î≤óÏñ¥ÎÇ† ÎïåÎßå ÏÉÅÌÉú Î≥ÄÍ≤Ω
      if (!e.relatedTarget || !(e.relatedTarget as Element).closest('.ProseMirror')) {
        setIsDragOver(false);
      }
    };

    const handleDrop = (e: DragEvent) => {
      e.preventDefault();
      setIsDragOver(false);
    };

    // Ï†ÑÏó≠ Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà Îì±Î°ù
    document.addEventListener('dragover', handleDragOver);
    document.addEventListener('dragleave', handleDragLeave);
    document.addEventListener('drop', handleDrop);

    return () => {
      document.removeEventListener('dragover', handleDragOver);
      document.removeEventListener('dragleave', handleDragLeave);
      document.removeEventListener('drop', handleDrop);
    };
  }, []);

  // üî• Î≥µÏÇ¨ Í∏∞Îä•ÏùÑ ÏúÑÌïú ÌÇ§Î≥¥Îìú Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà
  useEffect(() => {
    const handleCopy = (e: KeyboardEvent) => {
      const isCtrlC = (e.ctrlKey || e.metaKey) && e.key === 'c';
      const isCtrlA = (e.ctrlKey || e.metaKey) && e.key === 'a';
      
      if (isCtrlC || isCtrlA) {
        // Í∏∞Î≥∏ Î≥µÏÇ¨ ÎèôÏûë ÌóàÏö© (TipTapÏù¥ ÏûêÎèôÏúºÎ°ú Ï≤òÎ¶¨)
        console.log('Î≥µÏÇ¨ ÌóàÏö©:', isCtrlC ? 'Ctrl+C' : 'Ctrl+A');
      }
    };

    document.addEventListener('keydown', handleCopy);
    return () => document.removeEventListener('keydown', handleCopy);
  }, []);

  // üî• TipTap ÏóêÎîîÌÑ∞ Ï¥àÍ∏∞Ìôî (Notion Ïä§ÌÉÄÏùº + ÏûëÍ∞Ä ÏπúÌôîÏ†Å ÏÑ§Ï†ï)
  const editor = useEditor({
    extensions: [
      StarterKit.configure({
        // üî• ÏûëÍ∞Ä ÏπúÌôîÏ†Å ÏÑ§Ï†ï
        heading: {
          levels: [1, 2, 3, 4] // H1~H4Îßå ÏÇ¨Ïö©
        },
        bulletList: {
          HTMLAttributes: {
            class: 'list-disc list-outside ml-6'
          }
        },
        orderedList: {
          HTMLAttributes: {
            class: 'list-decimal list-outside ml-6'
          }
        }
      }),
      
      // üî• Ïñ∏ÎçîÎùºÏù∏ ÌôïÏû•
      Underline,
      
      // üî• Ïù¥ÎØ∏ÏßÄ ÌôïÏû• (ÎìúÎûòÍ∑∏Ïï§ÎìúÎ°≠, ÌÅ¥Î¶ΩÎ≥¥Îìú Î∂ôÏó¨ÎÑ£Í∏∞ ÏßÄÏõê)
      Image.configure({
        HTMLAttributes: {
          class: 'rounded-lg shadow-md max-w-full h-auto my-4',
        },
        inline: false,
        allowBase64: true,
      }),
      
      // üî• Placeholder ÌôïÏû• (ÏûëÍ∞Ä ÏπúÌôîÏ†Å)
      Placeholder.configure({
        placeholder: ({ node }) => {
          if (node.type.name === 'heading') {
            const level = node.attrs.level;
            switch (level) {
              case 1: return 'Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî...';
              case 2: return 'Ï±ïÌÑ∞ Ï†úÎ™©...';
              case 3: return 'ÏÑπÏÖò Ï†úÎ™©...';
              default: return 'ÏÜåÏ†úÎ™©...';
            }
          }
          if (node.type.name === 'callout') {
            return 'ÏΩúÏïÑÏõÉ ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî...';
          }
          if (node.type.name === 'toggle') {
            return 'ÌÜ†Í∏Ä ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî...';
          }
          return '/ Î•º ÏûÖÎ†•ÌïòÏó¨ Î™ÖÎ†πÏñ¥Î•º ÏÇ¨Ïö©ÌïòÍ±∞ÎÇò Ïù¥ÏïºÍ∏∞Î•º ÏãúÏûëÌï¥Î≥¥ÏÑ∏Ïöî...';
        },
        showOnlyWhenEditable: true,
        showOnlyCurrent: false,
      }),
      
      // üî• Focus ÌôïÏû• (Ìè¨Ïª§Ïä§ Î™®Îìú)
      Focus.configure({
        className: 'has-focus',
        mode: 'all',
      }),
      
      // üî• Typography ÌôïÏû• (ÏûëÍ∞Ä ÏπúÌôîÏ†Å ÌÉÄÏù¥Ìè¨Í∑∏ÎûòÌîº)
      Typography.configure({
        openDoubleQuote: '"',
        closeDoubleQuote: '"',
        openSingleQuote: "'",
        closeSingleQuote: "'",
        ellipsis: '...',
        emDash: '--',
      }),
      
      // üî• ÎÖ∏ÏÖò Ïä§ÌÉÄÏùº ÌôïÏû•Îì§
      TaskList,
      TaskItem,
      Callout,
      Toggle,
      Highlight,
      
      // üî• Ïä¨ÎûòÏãú Î™ÖÎ†πÏñ¥ ÌôïÏû•
      SlashCommand.configure({
        suggestion: slashSuggestion,
      }),
      
      // üî• Î¨∏Ïûê Ïàò Ïπ¥Ïö¥Ìä∏
      CharacterCount,
    ],
    
    content,
    
    // üî• ÏóêÎîîÌÑ∞ ÏÑ§Ï†ï
    editorProps: {
      attributes: {
        class: `${EDITOR_STYLES.editor} ${isFocusMode ? EDITOR_STYLES.focused : ''}`,
        'data-placeholder': 'Ïù¥ÏïºÍ∏∞Î•º ÏãúÏûëÌï¥Î≥¥ÏÑ∏Ïöî...',
      },
      
      // üî• TipTap Í≥µÏãù ÎßàÌÅ¨Îã§Ïö¥ Ï≤òÎ¶¨ Î∞©Ïãù (ÏôÑÏ†ÑÌûà ÎèôÍ∏∞Ï†Å Ïã§Ìñâ)
      handleKeyDown: (view, event) => {
        if (event.key === ' ') {
          const { state } = view;
          const { selection } = state;
          const { $from } = selection;
          const textBefore = $from.parent.textContent.slice(0, $from.parentOffset);
          
          // TipTapÏùò ÏóêÎîîÌÑ∞ Ïù∏Ïä§ÌÑ¥Ïä§Ïóê ÏßÅÏ†ë Ï†ëÍ∑º
          const editorInstance = (view as any).editor;
          if (!editorInstance) return false;
          
          // # Ï≤òÎ¶¨ (Ï†úÎ™© 1)
          if (textBefore === '#') {
            event.preventDefault();
            event.stopPropagation();
            
            // ÌÖçÏä§Ìä∏ ÏÇ≠Ï†ú ÌõÑ Ìó§Îî© Ï†ÅÏö©ÏùÑ Ï≤¥Ïù∏ÏúºÎ°ú Ïó∞Í≤∞
            editorInstance
              .chain()
              .focus()
              .deleteRange({ from: $from.pos - 1, to: $from.pos })
              .setHeading({ level: 1 })
              .run();
              
            Logger.debug('TIPTAP_EDITOR', '‚úÖ Markdown: H1 applied');
            return true;
          }
          
          // ## Ï≤òÎ¶¨ (Ï†úÎ™© 2)
          if (textBefore === '##') {
            event.preventDefault();
            event.stopPropagation();
            
            editorInstance
              .chain()
              .focus()
              .deleteRange({ from: $from.pos - 2, to: $from.pos })
              .setHeading({ level: 2 })
              .run();
              
            Logger.debug('TIPTAP_EDITOR', '‚úÖ Markdown: H2 applied');
            return true;
          }
          
          // ### Ï≤òÎ¶¨ (Ï†úÎ™© 3)
          if (textBefore === '###') {
            event.preventDefault();
            event.stopPropagation();
            
            editorInstance
              .chain()
              .focus()
              .deleteRange({ from: $from.pos - 3, to: $from.pos })
              .setHeading({ level: 3 })
              .run();
              
            Logger.debug('TIPTAP_EDITOR', '‚úÖ Markdown: H3 applied');
            return true;
          }
          
          // - Ï≤òÎ¶¨ (Î∂àÎ¶ø Î¶¨Ïä§Ìä∏)
          if (textBefore === '-') {
            event.preventDefault();
            event.stopPropagation();
            
            editorInstance
              .chain()
              .focus()
              .deleteRange({ from: $from.pos - 1, to: $from.pos })
              .toggleBulletList()
              .run();
              
            Logger.debug('TIPTAP_EDITOR', '‚úÖ Markdown: Bullet list applied');
            return true;
          }
          
          // 1. Ï≤òÎ¶¨ (Î≤àÌò∏ Î¶¨Ïä§Ìä∏)
          if (/^\d+\.$/.test(textBefore)) {
            event.preventDefault();
            event.stopPropagation();
            
            editorInstance
              .chain()
              .focus()
              .deleteRange({ from: $from.pos - textBefore.length, to: $from.pos })
              .toggleOrderedList()
              .run();
              
            Logger.debug('TIPTAP_EDITOR', '‚úÖ Markdown: Ordered list applied');
            return true;
          }
        }
        
        return false;
      },
      
      // üî• ÌÅ¥Î¶ΩÎ≥¥Îìú Ï≤òÎ¶¨ (Ïù¥ÎØ∏ÏßÄ Î∂ôÏó¨ÎÑ£Í∏∞ ÏßÄÏõê)
      handlePaste: (view, event) => {
        const editorInstance = (view as any).editor;
        if (!editorInstance) return false;
        
        const clipboardData = event.clipboardData;
        if (!clipboardData) return false;
        
        // Ïù¥ÎØ∏ÏßÄ ÌååÏùº Ï≤òÎ¶¨
        const items = Array.from(clipboardData.items);
        for (const item of items) {
          if (item.type.indexOf('image') === 0) {
            event.preventDefault();
            const file = item.getAsFile();
            if (file) {
              const reader = new FileReader();
              reader.onload = (e) => {
                const src = e.target?.result as string;
                if (src) {
                  editorInstance.chain().focus().setImage({ src }).run();
                  Logger.info('TIPTAP_EDITOR', 'Image pasted from clipboard');
                }
              };
              reader.readAsDataURL(file);
              return true;
            }
          }
        }
        
        // ÌÖçÏä§Ìä∏ Ï≤òÎ¶¨ (Í∏∞Î≥∏ ÎèôÏûë ÌóàÏö©)
        return false;
      },
      
      // üî• ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ Ï≤òÎ¶¨
      handleDrop: (view, event) => {
        const editorInstance = (view as any).editor;
        if (!editorInstance) return false;
        
        // ÎìúÎûòÍ∑∏ Ïò§Î≤Ñ ÌÅ¥ÎûòÏä§ Ï†úÍ±∞
        const editorElement = view.dom as HTMLElement;
        editorElement.classList.remove('drag-over');
        
        const files = Array.from(event.dataTransfer?.files || []);
        const imageFiles = files.filter(file => file.type.startsWith('image/'));
        
        if (imageFiles.length > 0) {
          event.preventDefault();
          
          imageFiles.forEach(file => {
            const reader = new FileReader();
            reader.onload = (e) => {
              const src = e.target?.result as string;
              if (src) {
                editorInstance.chain().focus().setImage({ src }).run();
                Logger.info('TIPTAP_EDITOR', 'Image dropped into editor');
              }
            };
            reader.readAsDataURL(file);
          });
          
          return true;
        }
        
        return false;
      },
    },
    
    // üî• ÏΩòÌÖêÏ∏† Î≥ÄÍ≤Ω Ìï∏Îì§Îü¨
    onUpdate: ({ editor }) => {
      const newContent = editor.getHTML();
      onChange(newContent);
      Logger.debug('TIPTAP_EDITOR', 'Content updated', { 
        wordCount: editor.storage.characterCount?.words() || 0 
      });
    },
    
    // üî• ÏóêÎîîÌÑ∞ Ï§ÄÎπÑ ÏôÑÎ£å
    onCreate: ({ editor }) => {
      setIsReady(true);
      Logger.info('TIPTAP_EDITOR', 'Editor created successfully');
    },
    
    // üî• ÏóêÎîîÌÑ∞ Ìè¨Ïª§Ïä§
    onFocus: ({ editor }) => {
      Logger.debug('TIPTAP_EDITOR', 'Editor focused');
    },
    
    // üî• ÏóêÎîîÌÑ∞ Î∏îÎü¨
    onBlur: ({ editor }) => {
      Logger.debug('TIPTAP_EDITOR', 'Editor blurred');
    },
  });

  // üî• Ïô∏Î∂Ä content Î≥ÄÍ≤Ω Ïãú ÏóêÎîîÌÑ∞ ÏóÖÎç∞Ïù¥Ìä∏
  useEffect(() => {
    if (editor && content !== editor.getHTML()) {
      editor.commands.setContent(content, false);
    }
  }, [content, editor]);

  // üî• Ìè¨Ïª§Ïä§ Î™®Îìú Î≥ÄÍ≤Ω Ïãú ÌÅ¥ÎûòÏä§ ÏóÖÎç∞Ïù¥Ìä∏
  useEffect(() => {
    if (editor) {
      const editorElement = editor.view.dom as HTMLElement;
      editorElement.className = `${EDITOR_STYLES.editor} ${isFocusMode ? EDITOR_STYLES.focused : ''}`;
    }
  }, [isFocusMode, editor]);

  // üî• Îã®Ï∂ïÌÇ§ Î∞îÏù∏Îî© Î∞è Ï†ÄÏû• Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà
  useEffect(() => {
    if (!editor) return;

    // üî• Îã®Ï∂ïÌÇ§ Î∞îÏù∏Îî©
    const unbindShortcuts = bindShortcutsToEditor(editor);
    
    // üî• ÌÅ¥Î¶ΩÎ≥¥Îìú Îã®Ï∂ïÌÇ§ Ï∂îÍ∞Ä
    const handleKeyDown = (event: KeyboardEvent) => {
      const { key, ctrlKey, metaKey } = event;
      const modKey = ctrlKey || metaKey; // Windows: Ctrl, Mac: Cmd
      
      // Ctrl/Cmd + C: Î≥µÏÇ¨
      if (modKey && key === 'c' && !event.shiftKey) {
        const selectedText = editor.state.doc.textBetween(
          editor.state.selection.from,
          editor.state.selection.to
        );
        if (selectedText) {
          navigator.clipboard.writeText(selectedText).then(() => {
            Logger.info('TIPTAP_EDITOR', 'Text copied via keyboard shortcut');
          }).catch((err) => {
            Logger.error('TIPTAP_EDITOR', 'Failed to copy text via shortcut', err);
          });
        }
      }
      
      // Ctrl/Cmd + V: Î∂ôÏó¨ÎÑ£Í∏∞
      if (modKey && key === 'v' && !event.shiftKey) {
        navigator.clipboard.readText().then((text) => {
          if (text) {
            editor.chain().focus().insertContent(text).run();
            Logger.info('TIPTAP_EDITOR', 'Text pasted via keyboard shortcut');
          }
        }).catch((err) => {
          Logger.error('TIPTAP_EDITOR', 'Failed to paste text via shortcut', err);
        });
      }
    };
    
    document.addEventListener('keydown', handleKeyDown);
    
    // üî• Ï†ÄÏû• Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà (Ctrl+S)
    const handleSave = () => {
      const saveEvent = new CustomEvent('project:save');
      window.dispatchEvent(saveEvent);
      Logger.info('TIPTAP_EDITOR', 'Save event triggered from editor');
    };
    
    window.addEventListener('editor:save', handleSave);
    
    Logger.info('TIPTAP_EDITOR', 'Shortcuts and save event bound', {
      shortcutCount: ALL_SHORTCUTS.length
    });

    // üî• Ï†ïÎ¶¨ Ìï®Ïàò
    return () => {
      unbindShortcuts();
      document.removeEventListener('keydown', handleKeyDown);
      window.removeEventListener('editor:save', handleSave);
      Logger.debug('TIPTAP_EDITOR', 'Shortcuts and events unbound');
    };
  }, [editor]);

  // üî• Ïª¥Ìè¨ÎÑåÌä∏ Ïñ∏ÎßàÏö¥Ìä∏ Ïãú Ï†ïÎ¶¨
  useEffect(() => {
    return () => {
      if (editor) {
        editor.destroy();
        Logger.debug('TIPTAP_EDITOR', 'Editor destroyed');
      }
    };
  }, [editor]);

  // üî• ESC ÌÇ§ Ìï∏Îì§Îü¨ (ÏßëÏ§ëÎ™®Îìú Ìï¥Ï†ú) Î∞è Î≥µÏÇ¨ Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà
  useEffect(() => {
    const handleEscKey = (event: KeyboardEvent): void => {
      if (event.key === 'Escape' && isFocusMode) {
        // ÏßëÏ§ëÎ™®Îìú Ìï¥Ï†ú Ïù¥Î≤§Ìä∏ Î∞úÏÉù
        const exitFocusEvent = new CustomEvent('editor:exitFocus');
        window.dispatchEvent(exitFocusEvent);
        Logger.info('TIPTAP_EDITOR', 'ESC pressed - exiting focus mode');
      }
    };

    // üî• QA Í∞ÄÏù¥Îìú: ÏóêÎîîÌÑ∞ ÎÇ¥Ïö© Î≥µÏÇ¨ Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà
    const handleCopyContent = (event: CustomEvent): void => {
      if (editor && event.detail && event.detail.callback) {
        const textContent = editor.getText();
        event.detail.callback(textContent);
        Logger.info('TIPTAP_EDITOR', 'Content copied via header button', { 
          length: textContent.length 
        });
      }
    };

    window.addEventListener('keydown', handleEscKey);
    window.addEventListener('project:copyContent', handleCopyContent as EventListener);
    
    return () => {
      window.removeEventListener('keydown', handleEscKey);
      window.removeEventListener('project:copyContent', handleCopyContent as EventListener);
    };
  }, [isFocusMode, editor]);

  // üî• ÏóêÎîîÌÑ∞ ÎìúÎûòÍ∑∏ Ïï§ ÎìúÎ°≠ ÏãúÍ∞ÅÏ†Å ÌîºÎìúÎ∞±
  useEffect(() => {
    if (!editor) return;

    const editorElement = editor.view.dom as HTMLElement;
    
    const handleDragEnter = (e: DragEvent) => {
      e.preventDefault();
      e.stopPropagation();
      editorElement.classList.add('drag-over');
      setIsDragOver(true);
    };

    const handleDragOver = (e: DragEvent) => {
      e.preventDefault();
      e.stopPropagation();
    };

    const handleDragLeave = (e: DragEvent) => {
      e.preventDefault();
      e.stopPropagation();
      // ÏóêÎîîÌÑ∞ ÏòÅÏó≠ÏùÑ ÏôÑÏ†ÑÌûà Î≤óÏñ¥ÎÇ† ÎïåÎßå ÏÉÅÌÉú Î≥ÄÍ≤Ω
      if (!editorElement.contains(e.relatedTarget as Node)) {
        editorElement.classList.remove('drag-over');
        setIsDragOver(false);
      }
    };

    const handleDrop = (e: DragEvent) => {
      e.preventDefault();
      e.stopPropagation();
      editorElement.classList.remove('drag-over');
      setIsDragOver(false);
    };

    // ÏóêÎîîÌÑ∞ Ï†ÑÏö© ÎìúÎûòÍ∑∏ Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà
    editorElement.addEventListener('dragenter', handleDragEnter);
    editorElement.addEventListener('dragover', handleDragOver);
    editorElement.addEventListener('dragleave', handleDragLeave);
    editorElement.addEventListener('drop', handleDrop);

    return () => {
      editorElement.removeEventListener('dragenter', handleDragEnter);
      editorElement.removeEventListener('dragover', handleDragOver);
      editorElement.removeEventListener('dragleave', handleDragLeave);
      editorElement.removeEventListener('drop', handleDrop);
    };
  }, [editor]);

  // üî• Î°úÎî© Ï§ë ÌëúÏãú
  if (!isReady) {
    return (
      <div className={EDITOR_STYLES.container}>
        <div className="flex items-center justify-center h-full">
          <div className="flex flex-col items-center gap-4">
            <div className="w-8 h-8 border-2 border-blue-500 border-t-transparent rounded-full animate-spin"></div>
            <span className="text-slate-500 text-sm">ÏóêÎîîÌÑ∞ Ï§ÄÎπÑ Ï§ë...</span>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className={`${EDITOR_STYLES.container} ${isDragOver ? 'drag-over' : ''}`}>
      {/* üî• ÎìúÎûòÍ∑∏ Ïò§Î≤Ñ ÏÉÅÌÉú ÌîºÎìúÎ∞± */}
      {isDragOver && (
        <div className="absolute inset-0 border-2 border-dashed border-blue-500 bg-blue-50/50 dark:bg-blue-900/20 rounded-lg flex items-center justify-center z-10 pointer-events-none">
          <div className="text-blue-600 dark:text-blue-400 text-lg font-medium">
            üìÅ ÌååÏùºÏùÑ Ïó¨Í∏∞Ïóê ÎÜìÏúºÏÑ∏Ïöî
          </div>
        </div>
      )}
      
      {/* üî• Enhanced Bubble Menu (ÏÑ†ÌÉù Ïãú ÎÇòÌÉÄÎÇòÎäî Í≥†Í∏â Ìà¥Î∞î) */}
      {editor && (
        <BubbleMenu 
          editor={editor} 
          className={EDITOR_STYLES.bubble}
          shouldShow={({ editor, view, state, oldState, from, to }) => {
            // ÌÖçÏä§Ìä∏Í∞Ä ÏÑ†ÌÉùÎêòÏóàÏùÑ ÎïåÎßå ÌëúÏãú
            return from !== to;
          }}
        >
          {/* Í∏∞Î≥∏ Ìè¨Îß∑ÌåÖ Î≤ÑÌäºÎì§ */}
          <button
            onClick={() => editor.chain().focus().toggleBold().run()}
            className={`${EDITOR_STYLES.bubbleButton} ${
              editor.isActive('bold') ? 'bg-blue-200 dark:bg-blue-800' : ''
            }`}
            title="Î≥ºÎìú (Ctrl+B)"
          >
            <Bold size={14} />
          </button>
          
          <button
            onClick={() => editor.chain().focus().toggleItalic().run()}
            className={`${EDITOR_STYLES.bubbleButton} ${
              editor.isActive('italic') ? 'bg-blue-200 dark:bg-blue-800' : ''
            }`}
            title="Ïù¥ÌÉ§Î¶≠ (Ctrl+I)"
          >
            <Italic size={14} />
          </button>
          
          <button
            onClick={() => editor.chain().focus().toggleUnderline().run()}
            className={`${EDITOR_STYLES.bubbleButton} ${
              editor.isActive('underline') ? 'bg-blue-200 dark:bg-blue-800' : ''
            }`}
            title="Ïñ∏ÎçîÎùºÏù∏ (Ctrl+U)"
          >
            <UnderlineIcon size={14} />
          </button>
          
          <button
            onClick={() => editor.chain().focus().toggleStrike().run()}
            className={`${EDITOR_STYLES.bubbleButton} ${
              editor.isActive('strike') ? 'bg-blue-200 dark:bg-blue-800' : ''
            }`}
            title="Ï∑®ÏÜåÏÑ† (Ctrl+Shift+S)"
          >
            <Strikethrough size={14} />
          </button>
          
          {/* Íµ¨Î∂ÑÏÑ† */}
          <div className="w-px h-6 bg-gray-300 dark:bg-gray-600 mx-1" />
          
          {/* Í≥†Í∏â Ìè¨Îß∑ÌåÖ */}
          <button
            onClick={() => editor.chain().focus().toggleCode().run()}
            className={`${EDITOR_STYLES.bubbleButton} ${
              editor.isActive('code') ? 'bg-blue-200 dark:bg-blue-800' : ''
            }`}
            title="Ïù∏ÎùºÏù∏ ÏΩîÎìú (Ctrl+`)"
          >
            <Code size={14} />
          </button>
          
          {/* Íµ¨Î∂ÑÏÑ† */}
          <div className="w-px h-6 bg-gray-300 dark:bg-gray-600 mx-1" />
          
          {/* ÎßÅÌÅ¨ Î≤ÑÌäº */}
          <button
            onClick={() => {
              // TODO: ÎßÅÌÅ¨ Îã§Ïù¥ÏñºÎ°úÍ∑∏ Î™®Îã¨Î°ú ÍµêÏ≤¥ ÌïÑÏöî
              console.log('ÎßÅÌÅ¨ Í∏∞Îä•ÏùÄ Ï∂îÌõÑ Îã§Ïù¥ÏñºÎ°úÍ∑∏Î°ú Íµ¨ÌòÑ ÏòàÏ†ï');
            }}
            className={`${EDITOR_STYLES.bubbleButton} ${
              editor.isActive('link') ? 'bg-blue-200 dark:bg-blue-800' : ''
            }`}
            title="ÎßÅÌÅ¨ Ï∂îÍ∞Ä"
          >
            <Link size={14} />
          </button>
          
          {/* Ïù∏Ïö©Íµ¨ Î≤ÑÌäº */}
          <button
            onClick={() => editor.chain().focus().toggleBlockquote().run()}
            className={`${EDITOR_STYLES.bubbleButton} ${
              editor.isActive('blockquote') ? 'bg-blue-200 dark:bg-blue-800' : ''
            }`}
            title="Ïù∏Ïö©Íµ¨"
          >
            <Quote size={14} />
          </button>
          
          {/* Íµ¨Î∂ÑÏÑ† */}
          <div className="w-px h-6 bg-gray-300 dark:bg-gray-600 mx-1" />
          
          {/* Ïù¥ÎØ∏ÏßÄ Ï∂îÍ∞Ä Î≤ÑÌäº */}
          <button
            onClick={() => {
              const input = document.createElement('input');
              input.type = 'file';
              input.accept = 'image/*';
              input.onchange = (e) => {
                const file = (e.target as HTMLInputElement).files?.[0];
                if (file) {
                  const reader = new FileReader();
                  reader.onload = (event) => {
                    const src = event.target?.result as string;
                    if (src) {
                      editor.chain().focus().setImage({ src }).run();
                      Logger.info('TIPTAP_EDITOR', 'Image added via file picker');
                    }
                  };
                  reader.readAsDataURL(file);
                }
              };
              input.click();
            }}
            className={EDITOR_STYLES.bubbleButton}
            title="Ïù¥ÎØ∏ÏßÄ Ï∂îÍ∞Ä"
          >
            <ImageIcon size={14} />
          </button>
          
          {/* Î≥µÏÇ¨ Î≤ÑÌäº */}
          <button
            onClick={() => {
              const selectedText = editor.state.doc.textBetween(
                editor.state.selection.from,
                editor.state.selection.to
              );
              if (selectedText) {
                navigator.clipboard.writeText(selectedText).then(() => {
                  Logger.info('TIPTAP_EDITOR', 'Text copied to clipboard');
                }).catch((err) => {
                  Logger.error('TIPTAP_EDITOR', 'Failed to copy text', err);
                });
              }
            }}
            className={EDITOR_STYLES.bubbleButton}
            title="ÏÑ†ÌÉùÌïú ÌÖçÏä§Ìä∏ Î≥µÏÇ¨"
          >
            <Copy size={14} />
          </button>
          
          {/* ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóêÏÑú Î∂ôÏó¨ÎÑ£Í∏∞ Î≤ÑÌäº */}
          <button
            onClick={async () => {
              try {
                const text = await navigator.clipboard.readText();
                if (text) {
                  editor.chain().focus().insertContent(text).run();
                  Logger.info('TIPTAP_EDITOR', 'Text pasted from clipboard');
                }
              } catch (err) {
                Logger.error('TIPTAP_EDITOR', 'Failed to paste from clipboard', err);
              }
            }}
            className={EDITOR_STYLES.bubbleButton}
            title="ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóêÏÑú Î∂ôÏó¨ÎÑ£Í∏∞"
          >
            <Clipboard size={14} />
          </button>
          
          {/* Íµ¨Î∂ÑÏÑ† */}
          <div className="w-px h-6 bg-gray-300 dark:bg-gray-600 mx-1" />
          
          {/* Ï∂îÍ∞Ä ÏòµÏÖò */}
          <button
            onClick={() => {
              // Í∏∞Î≥∏ÏúºÎ°ú H2 Ìó§Îî© Ï†ÅÏö© (prompt ÎåÄÏã†)
              editor.chain().focus().setHeading({ level: 2 }).run();
            }}
            className={EDITOR_STYLES.bubbleButton}
            title="Ìó§Îî© ÏÑ§Ï†ï"
          >
            <MoreHorizontal size={14} />
          </button>
        </BubbleMenu>
      )}
      
      {/* üî• Î©îÏù∏ ÏóêÎîîÌÑ∞ */}
      <EditorContent editor={editor} />
    </div>
  );
}
